let playMsg = document.getElementById("playMsg");
let warpPlay = document.getElementById("wrap");
let videoMsg = document.getElementById("videoMsg");
let managerMsg = document.getElementById("managerMsg");
let controls = null;
let timeline3 = null;
if (managerMsg){
    controls = document.getElementById("managerMsg").querySelector(".controls");
    timeline3 = document.getElementById("managerMsg").querySelector('.timeline');
}
let msgPlay = document.getElementById("msgPlay");
const refreshMsg = document.getElementById('refreshMsg');
let repeated2 = false;


if (playMsg){
    playMsg.addEventListener("click" , ()=>{
        videoMsg.play();
        warpPlay.style.display = "none";
        controls.style.display = "block";
        msgPlay.src = "tmp/pause.svg";
        msgPlay.style.width = '40%';
    })
}

if (msgPlay){
    msgPlay.addEventListener("click" , ()=>{
        if (videoMsg.paused){
            videoMsg.play();
            msgPlay.src = "tmp/pause.svg";
            msgPlay.style.width = '40%';
        }else {
            videoMsg.pause();
            msgPlay.src = "tmp/play.svg";
            msgPlay.style.width = 'auto';
        }
    })
}

if (videoMsg){
    videoMsg.ontimeupdate = function () {

        const percentagePosition = (100 * videoMsg.currentTime) / videoMsg.duration;
        timeline3.style.backgroundSize = `${percentagePosition}% 100%`;
        timeline3.value = percentagePosition;
    }
}

if (timeline3){
    timeline3.addEventListener('change', function () {

        videoMsg.pause();
        const time = (timeline3.value * videoMsg.duration) / 100;
        videoMsg.currentTime = time;
        msgPlay.src = "tmp/play.svg";
        msgPlay.style.width = 'auto';


    });
}

if (refreshMsg){
    refreshMsg.addEventListener("click", () => {
        refreshMsg.classList.toggle("toggleFeature2");
        repeated2 = !repeated2;
    })
}

if (videoMsg){
    videoMsg.onended = function () {

        msgPlay.src = "tmp/play.svg";
        msgPlay.style.width = 'auto';


        if (repeated2) {
            msgPlay.src = "tmp/pause.svg";
            msgPlay.style.width = '40%';
            videoMsg.play();
        }
    }
}