let bigImg2 = document.getElementById("bigImg2");
let imagesOf2 = document.querySelectorAll(".rep-img");

for (let y = 0; y <= imagesOf2.length; y++){
    if (imagesOf2[y]){
        imagesOf2[y].addEventListener("click" , ()=>{
            bigImg2.querySelector("img").src = imagesOf2[y].querySelector(".repp").src;
            bigImg2.classList.add("bigImgShow")
        })
    }
}

window.addEventListener("click" , (e)=>{
    if (e.target.id === "bigImg2"){
        bigImg2.classList.remove("bigImgShow")
    }
})