let tabs = document.getElementsByClassName("tab-title-acc");
let tabsContent = document.getElementsByClassName("tab-content-acc");
let contentId = null;
for (let i = 0; i <= tabs.length; i++) {
    if (tabs[i]) {
        tabs[i].addEventListener("click", () => {

            for (let j = 0; j <= tabs.length; j++) {
                if (tabs[j] && tabsContent[j]){
                    tabs[j].classList.remove("active-acc")
                    tabsContent[j].classList.remove("active-tab-content")
                }
            }

            tabs[i].classList.add("active-acc")
            contentId = tabs[i].dataset.tab;
            document.getElementById(contentId).classList.add("active-tab-content")

        })
    }
}