const play = `<img src="tmp/play.svg" alt="">`;
const pause = `<img src="tmp/pause.svg" width="40%" alt="">`;
const sound = `<svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15.536 8.464a5 5 0 010 7.072m2.828-9.9a9 9 0 010 12.728M5.586 15H4a1 1 0 01-1-1v-4a1 1 0 011-1h1.586l4.707-4.707C10.923 3.663 12 4.109 12 5v14c0 .891-1.077 1.337-1.707.707L5.586 15z" />
</svg>`;
const mute = `<svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M5.586 15H4a1 1 0 01-1-1v-4a1 1 0 011-1h1.586l4.707-4.707C10.923 3.663 12 4.109 12 5v14c0 .891-1.077 1.337-1.707.707L5.586 15z" clip-rule="evenodd" />
  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2" />
</svg>`;


const playButton = document.querySelector('.play-button');
const video = document.getElementById('video');
const timeline = document.querySelector('.timeline');
const soundButton = document.querySelector('.sound-button');
const fullscreenButton = document.querySelector('.fullscreen-button');
const videoContainer = document.querySelector('.video-player');
const prevVid = document.getElementById("prevVid");
const playVid = document.getElementById("playVid");
const nextVid = document.getElementById("nextVid");
const shuffleVid = document.getElementById('shuffleVid');
const refreshVid = document.getElementById('refreshVid');
let videoSlider = document.getElementById('video-slider');
let videoSlides = null;
if (videoSlider){
    videoSlides = videoSlider.querySelectorAll('.video-player');
}
let isFullScreen = false;

if (playButton){
    playButton.addEventListener('click', function () {

        let currentVideo = null;
        for (let c = 0; c <= videoSlides.length; c++) {
            if (videoSlides[c]) {
                if (video.querySelector("source").src === videoSlides[c].querySelector("source").src) {
                    currentVideo = videoSlides[c];
                }
            }
        }

        if (video.paused) {
            video.play();
            currentVideo.querySelector("video").play();
            videoContainer.classList.add('playing');
            playButton.innerHTML = pause;
            playVid.src = "tmp/ppp.svg";
            currentVideo.querySelector("img").src = "tmp/ppp.svg";

        } else {
            video.pause();
            currentVideo.querySelector("video").pause();
            videoContainer.classList.remove('playing');
            playButton.innerHTML = play;
            playVid.src = "tmp/vid-play.svg";
            currentVideo.querySelector("img").src = "tmp/vid-play.svg";
        }
    })
}

if (playVid){
    playVid.addEventListener('click', function () {
        let currentVideo = null;
        for (let c = 0; c <= videoSlides.length; c++) {
            if (videoSlides[c]) {
                if (video.querySelector("source").src === videoSlides[c].querySelector("source").src) {
                    currentVideo = videoSlides[c];
                }
            }
        }

        if (video.paused) {
            video.play();
            currentVideo.querySelector("video").play();
            videoContainer.classList.add('playing');
            playButton.innerHTML = pause;
            playVid.src = "tmp/ppp.svg";
            currentVideo.querySelector("img").src = "tmp/ppp.svg";
        } else {
            video.pause();
            currentVideo.querySelector("video").pause();
            videoContainer.classList.remove('playing');
            playButton.innerHTML = play;
            playVid.src = "tmp/vid-play.svg";
            currentVideo.querySelector("img").src = "tmp/vid-play.svg";
        }
    })
}


if (video){
    video.ontimeupdate = function () {

        const percentagePosition = (100 * video.currentTime) / video.duration;
        timeline.style.backgroundSize = `${percentagePosition}% 100%`;
        timeline.value = percentagePosition;
    }
}


if (timeline){
    timeline.addEventListener('change', function () {

        video.pause();
        const time = (timeline.value * video.duration) / 100;
        video.currentTime = time;
        playButton.innerHTML = play;
        playVid.src = "tmp/vid-play.svg";

        let currentVideo2 = null;
        for (let c = 0; c <= videoSlides.length; c++) {
            if (videoSlides[c]) {
                if (video.querySelector("source").src === videoSlides[c].querySelector("source").src) {
                    currentVideo2 = videoSlides[c].querySelector("video");
                    currentVideo2.pause();
                    currentVideo2.currentTime = time;
                }
            }
        }

    });
}


if (videoSlides){
    for (let i = 0; i <= videoSlides.length; i++) {
        if (videoSlides[i]) {

            videoSlides[i].querySelector("#playVid").addEventListener('click', (e) => {


                if (video.querySelector("source").src === videoSlides[i].querySelector("source").src && videoSlides[i].querySelector("video").paused) {
                    video.play();
                    videoSlides[i].querySelector("video").play();
                    videoSlides[i].querySelector("#playVid").src = "tmp/ppp.svg"
                    playButton.innerHTML = pause;
                    playVid.src = "tmp/ppp.svg";
                } else if (video.querySelector("source").src === videoSlides[i].querySelector("source").src) {

                    video.pause();
                    videoSlides[i].querySelector("video").pause();
                    videoSlides[i].querySelector("#playVid").src = "tmp/vid-play.svg"
                    playButton.innerHTML = play;
                    playVid.src = "tmp/vid-play.svg";

                } else {


                    for (let m = 0; m <= videoSlides.length; m++) {
                        if (videoSlides[m]) {
                            videoSlides[m].querySelector("video").classList.remove("activePlay");
                            videoSlides[m].querySelector("#playVid").src = "tmp/vid-play.svg"
                            videoSlides[m].querySelector("video").pause();
                            videoSlides[m].querySelector("video").currentTime = 0;
                        }
                    }


                    videoSlides[i].querySelector("video").classList.add("activePlay");
                    video.pause();
                    video.querySelector("source").src = videoSlides[i].querySelector("source").src;
                    video.load();
                    video.play();
                    videoSlides[i].querySelector("video").play();
                    videoSlides[i].querySelector("#playVid").src = "tmp/ppp.svg"
                    playButton.innerHTML = pause;
                    playVid.src = "tmp/ppp.svg";
                }


                let currentVideo = null;
                for (let c = 0; c <= videoSlides.length; c++) {
                    if (videoSlides[c]) {
                        if (video.querySelector("source").src === videoSlides[c].querySelector("source").src) {
                            currentVideo = videoSlides[c].querySelector("video");

                            currentVideo.ontimeupdate = function () {

                                const percentagePosition = (100 * videoSlides[c].querySelector("video").currentTime) / videoSlides[c].querySelector("video").duration;
                                videoSlides[c].querySelector(".timeline").style.backgroundSize = `${percentagePosition}% 100%`;
                                videoSlides[c].querySelector(".timeline").value = percentagePosition;

                            }
                        }
                    }
                }


            })
        }
    }
}

if (prevVid){
    prevVid.addEventListener('click', () => {
        for (let j = 0; j <= videoSlides.length; j++) {
            if (videoSlides[j]) {


                if (video.querySelector("source").src === videoSlides[j].querySelector("source").src) {
                    let prevNum = j - 1;
                    if (prevNum >= 0) {

                        videoSlides[j].querySelector("video").classList.remove("activePlay");
                        videoSlides[j].querySelector("#playVid").src = "tmp/vid-play.svg"
                        videoSlides[j].querySelector("video").pause();
                        videoSlides[j].querySelector("video").currentTime = 0;

                        video.pause();
                        video.querySelector("source").src = videoSlides[prevNum].querySelector("source").src;
                        video.load();
                        video.play();

                        // for prev video effect in slider
                        videoSlides[prevNum].querySelector("video").classList.add("activePlay");
                        videoSlides[prevNum].querySelector("#playVid").src = "tmp/ppp.svg"
                        videoSlides[prevNum].querySelector("video").currentTime = 0;
                        videoSlides[prevNum].querySelector("video").play();
                    }
                }
            }
        }
    })
}

if (nextVid){
    nextVid.addEventListener('click', () => {
        for (let k = 0; k <= videoSlides.length; k++) {
            if (videoSlides[k]) {

                if (video.querySelector("source").src === videoSlides[k].querySelector("source").src) {
                    let nextNum = k + 1;
                    if (nextNum < videoSlides.length) {

                        videoSlides[k].querySelector("video").classList.remove("activePlay");
                        videoSlides[k].querySelector("#playVid").src = "tmp/vid-play.svg"
                        videoSlides[k].querySelector("video").pause();
                        videoSlides[k].querySelector("video").currentTime = 0;


                        video.pause();
                        video.querySelector("source").src = videoSlides[nextNum].querySelector("source").src;
                        video.load();
                        video.play();


                        // for prev video effect in slider
                        videoSlides[nextNum].querySelector("video").classList.add("activePlay");
                        videoSlides[nextNum].querySelector("#playVid").src = "tmp/ppp.svg"
                        videoSlides[nextNum].querySelector("video").currentTime = 0;
                        videoSlides[nextNum].querySelector("video").play();

                        break;


                    }
                }
            }
        }
    })
}

let shuffled = false;
let repeated = false;
if (shuffleVid){
    shuffleVid.addEventListener("click", () => {
        shuffleVid.classList.toggle("toggleFeature");
        shuffled = !shuffled;
    })
}

if (refreshVid){
    refreshVid.addEventListener("click", () => {
        refreshVid.classList.toggle("toggleFeature");
        repeated = !repeated;
    })
}

if (video){
    video.onended = function () {

        let currentVideo = null;
        for (let c = 0; c <= videoSlides.length; c++) {
            if (videoSlides[c]) {
                if (video.querySelector("source").src === videoSlides[c].querySelector("source").src) {
                    currentVideo = videoSlides[c];
                }
            }
        }

        playButton.innerHTML = play;
        playVid.src = "tmp/vid-play.svg";
        currentVideo.querySelector("img").src = "tmp/vid-play.svg";

        if (shuffled) {
            const rndInt = Math.floor(Math.random() * videoSlides.length);

            for (let x = 0; x <= videoSlides.length; x++) {
                if (videoSlides[x]) {
                    videoSlides[x].querySelector("video").classList.remove("activePlay");
                    videoSlides[x].querySelector("#playVid").src = "tmp/vid-play.svg"
                    videoSlides[x].querySelector("video").pause();
                    videoSlides[x].querySelector("video").currentTime = 0;
                }
            }

            video.pause();
            video.querySelector("source").src = videoSlides[rndInt].querySelector("source").src;
            video.load();
            video.play();


            // for prev video effect in slider
            videoSlides[rndInt].querySelector("video").classList.add("activePlay");
            videoSlides[rndInt].querySelector("#playVid").src = "tmp/ppp.svg"
            videoSlides[rndInt].querySelector("video").currentTime = 0;
            videoSlides[rndInt].querySelector("video").play();

        }

        if (repeated) {
            playButton.innerHTML = pause;
            playVid.src = "tmp/ppp.svg";
            currentVideo.querySelector("img").src = "tmp/ppp.svg";
            video.play();
            currentVideo.querySelector("video").play();
        }

    }
}



let bigImg = document.getElementById("bigImg");
let imagesOf = document.querySelectorAll(".gallery-bg .hover-image");

for (let y = 0; y <= imagesOf.length; y++){
    if (imagesOf[y]){
        imagesOf[y].addEventListener("click" , ()=>{
            bigImg.querySelector("img").src = imagesOf[y].nextElementSibling.src;
            bigImg.classList.add("bigImgShow")
        })
    }
}

window.addEventListener("click" , (e)=>{
    if (e.target.id === "bigImg"){
        bigImg.classList.remove("bigImgShow")
    }
})