let menuItems = document.getElementsByClassName("menu");

for (let i = 0; i <= menuItems.length; i++) {
  if (menuItems[i]) {
    let submenuItems = menuItems[i].querySelector(".sub-menu");
    if (submenuItems) {
      menuItems[i].addEventListener("click", (e) => {
        e.stopPropagation();
        submenuItems.classList.toggle("men");
      });

      let subMenu = submenuItems.querySelectorAll("li");

      for (let j = 0; j <= subMenu.length; j++) {
        if (subMenu[j]) {
          let subSub = subMenu[j].querySelector(".sub-sub-menu");
          if (subSub) {
            subMenu[j].addEventListener("click", (e) => {
              e.stopPropagation();
              subSub.classList.toggle("men");
            });
          }
        }
      }
    }
  }
}

let navbar = document.getElementById("navbar");
let underNav = document.getElementById("underNav");
let mobMenuBtn = document.getElementsByClassName("mob-menu")[0];
let mobSearch = document.getElementsByClassName("mob-search")[0];
let mobSearchView = document.getElementById("mobSearchView");

if (mobMenuBtn) {
  mobMenuBtn.addEventListener('click', () => {
    navbar.style.right = 0;
    underNav.style.opacity = 1;
    underNav.style.visibility = 'visible';
  })
}

window.addEventListener('click', (e) => {
if (navbar){
  if(e.target.id === 'underNav'){
    navbar.style.right = '-18em';
    underNav.style.opacity = 0;
    underNav.style.visibility = "hidden";
  } else if (e.target.id === "mobSearchView") {
    mobSearchView.style.opacity = 0;
    mobSearchView.style.visibility = "hidden";
  }
}
})

if (mobSearch) {
  mobSearch.addEventListener('click', () => {
    mobSearchView.classList.add("trans");
    mobSearchView.style.opacity = 1;
    mobSearchView.style.visibility = 'visible';
  })
}

let nv3 = document.getElementsByClassName('nv3')[0];

let screenSize = screen.width;
window.addEventListener("resize" , ()=>{
  screenSize = screen.width;
})

if (navbar){
    window.onscroll = function() {
      if (screenSize > 992){
      let currentScrollPos = window.pageYOffset;
      if (0 >= currentScrollPos) {
        navbar.style.top = "0";
        nv3.classList.remove("nv3Show");
      } else {
        navbar.style.top = "-15em";
        nv3.classList.add("nv3Show");
      }
    }
  }
}
