let miniMenuOpener = document.getElementById("miniMenuOpener");
let slideMiniMenu = document.getElementById("slideMiniMenu");
let miniMenuOpener2 = document.getElementById("miniMenuOpener2");
let slideMiniMenu2 = document.getElementById("slideMiniMenu2");

if (miniMenuOpener) {
    miniMenuOpener.addEventListener("click", () => {
        slideMiniMenu.classList.toggle("openmini");
    });
}

if (miniMenuOpener2) {
    miniMenuOpener2.addEventListener("click", () => {
        slideMiniMenu2.classList.toggle("openmini2");
    });
}


